  &.@{inputPrefixCls}-disabled {
    .@{inputPrefixCls}-label {
      color: @color-text-disabled;
    }
  }
}

.@{listPrefixCls} {
  &-body {
    .@{inputPrefixCls}-item {
      &:last-child {
        .hairline-remove-right-bottom();
      }
    }
  }
}



